@import url('https://fonts.googleapis.com/css2?family=Hammersmith+One&display=swap');

.input {
    margin: 2rem auto;
    padding: 1rem;
    width: 90%;
    max-width: 40rem;
  }
  
  .input label {
    display: block;
    font-weight: regular;
    margin-bottom: 0.5rem;
    font-family: 'Hammersmith One', sans-serif;
    font-size: x-large;
  }
  
  .input input {
    font: inherit;
    display: block;
    width: 100%;
    height: 35px;
    border: 1.5px solid rgb(80, 79, 79);
    padding: 0.15rem;
    margin-bottom: 0.5rem;
    border-radius: 5px;
  }
  
  .input input:focus {
    outline: none;
    border-color: #4f005f;
  }
  
  .input input::placeholder{
    color: #6b6b6b;
    padding-left: 7px;
  }

  